import { Component, Host, h, Prop, State, Listen } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { IconName } from '../../atoms/icon/icon.types';
import { throttle } from '../../../utils/throttle';
import { Breakpoint, compareBreakpoint } from '../../../utils/breakpoints';
import { HeaderNavigation } from '../overlay-menu/overlay-menu.types';

/**
 * Header navigation component.
 */
@Component({
  tag: 'bs-header-navigation',
  styleUrl: 'header-navigation.scss',
  shadow: true,
})
export class BsHeaderNavigation {
  /**
   * State
   */

  @State() stickyMenuIsVisible: boolean;

  /**
   * The .
   */
  @Prop() headerNavigation: HeaderNavigation | string;

  @Memoize('headerNavigation')
  get parsedHeaderNavigation(): HeaderNavigation {
    return parseIfString(this.headerNavigation);
  }

  /**
   * Listeners
   */
  @Listen('scroll', { target: 'window' })
  handleBodyScroll() {
    throttle(() => {
      this.stickyMenuIsVisible = window.scrollY > 20;
    }, 500)();
  }

  render() {
    return (
      <Host>
        <header class="header-navigation">
          {/* Main Logo */}
          <div class="bar">
            <bs-container class="flex-between" contained>
              <div class="main-logo">
                <a href="/" tabIndex={this.stickyMenuIsVisible ? -1 : 0}>
                  {compareBreakpoint('max', Breakpoint.md) ? (
                    <bs-image class="logo" {...this.parsedHeaderNavigation.mobileMainLogo} />
                  ) : (
                    <bs-image class="logo" {...this.parsedHeaderNavigation.mainLogo} />
                  )}
                </a>
              </div>
              {/* Menu button */}
              <bs-click-action
                tag-class={`menu-button`}
                value={this.headerNavigation}
                name="open-overlay-menu"
              >
                <bs-icon name={IconName.Menu} />
              </bs-click-action>
            </bs-container>
          </div>

          {/* Sticky header */}
          <div class={`sticky-header ${this.stickyMenuIsVisible ? 'is-visible' : ''}`}>
            <bs-container class="flex-between" contained>
              <div class="logo">
                <a href="/" tabIndex={this.stickyMenuIsVisible ? 0 : -1}>
                  <bs-image class="logo" {...this.parsedHeaderNavigation.stickyLogo} />
                </a>
              </div>

              {/* Menu button */}
              <div class="flex-end">
                {this.parsedHeaderNavigation.stickyButton &&
                  this.parsedHeaderNavigation.stickyButton.text &&
                  compareBreakpoint('min', Breakpoint.md) && (
                    <bs-button {...this.parsedHeaderNavigation.stickyButton} />
                  )}
                <bs-click-action
                  tag-class={`menu-button`}
                  value={this.headerNavigation}
                  name="open-overlay-menu"
                >
                  <bs-icon name={IconName.Menu} />
                </bs-click-action>
              </div>
            </bs-container>
          </div>
        </header>
      </Host>
    );
  }
}
