:host {
  --navigation--height: 60px;
  --image--max-height: 40px;
  --image--max-width: 130px;
  --image--fit: 'contain';

  @include mq('min', 'md') {
    --navigation--height: 92px;
    --image--max-height: 60px;
    --image--max-width: 200px;
  }
}

:host .header-navigation {
  @include z-index('header');

  position: relative;
  height: var(--navigation--height);
}

:host .bar {
  display: flex;
  align-items: center;
  width: 100%;
  height: var(--navigation--height);
  background-color: $white;
}

:host .sticky-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  width: 100%;
  height: var(--navigation--height);
  background-color: $white;
  border-bottom: 1px solid $light-gray;

  transition: $transition-smooth;
  transition-property: transform;
  transform: translateY(-110%);
}

:host .flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

:host .flex-end {
  display: flex;
  gap: rem(40);
  align-items: center;
  justify-content: flex-end;
}

:host .is-visible {
  transform: translateY(0);
}

:host .logo {
  padding: rem(10);
  padding-left: 0;
}

:host .menu-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: rem(10);
  padding-right: 0;

  font: inherit;
  line-height: 1;
  color: $black;
  text-transform: uppercase;

  pointer-events: visible;
  cursor: pointer;
  background: transparent;
  border: 0;
  border-radius: rem(5);

  @include mq('max', 'md') {
    right: rem(25);
  }

  .menu-button-text {
    @include mq('max', 'md') {
      @include sr-only;
    }
  }

  > bs-icon {
    margin-left: rem(7);
    font-size: rem(21);

    @include mq('max', 'md') {
      font-size: rem(30);
    }
  }
}
